<template>
  <v-container fluid>
    <v-row no-gutters>
      <v-col cols="12">
        <v-text-field label="Nama playlist" v-model="playlist.name"></v-text-field>
      </v-col>
    </v-row>
    <v-row no-gutters>
      <v-col cols="10">
        Rangkaian Asset
      </v-col>
      <v-col cols="2" class="text-right">
        <v-icon @click="onAddAssetClick">mdi-plus</v-icon>
      </v-col>
    </v-row>
    <v-list>
      <template v-for="(asset, index) of playlist.data">
        <v-list-item :key="`${asset[0]}_${index}`">
            <v-row no-gutters>
              <v-col cols="12" md="9">{{ asset[1] }}</v-col>
              <v-col cols="12" md="1" class="text-right">
                {{ asset[3] }} detik
              </v-col>
              <v-col cols="12" md="2" class="text-right">
                <v-icon v-if="index!==(playlist.data.length-1)" @click="moveDownAsset(index)">
                  mdi-arrow-down
                </v-icon>
                <v-icon v-if="index!==0" @click="moveUpAsset(index)">
                  mdi-arrow-up
                </v-icon>
                <v-icon @click="deleteAsset(index)">mdi-delete-forever</v-icon>
                <v-icon @click="onAlarmClick(asset, index)">mdi-clock-outline</v-icon>
              </v-col>
            </v-row>
        </v-list-item>
        <v-divider :key="index" v-if="index!==(playlist.data.length-1)"></v-divider>
      </template>
    </v-list>
    <v-row no-gutters>
      <v-col cols="12" class="text-center">
        <v-btn color="primary" @click="onCancelClick" :disabled="loading">Batal</v-btn>
        &nbsp;
        <v-btn color="primary" @click="onSaveClick" :disabled="!valid"
          :loading="loading"
        >Save</v-btn>
      </v-col>
    </v-row>
    <v-dialog max-width="360" v-model="dialogAssetList">
      <v-card>
        <v-list>
          <template v-for="(asset, index) in assets">
            <v-list-item :key="asset[0]" @click="onAssetListClick(asset)">
              <v-row no-gutters>
                <v-col cols="12">{{ asset[1] }}</v-col>
              </v-row>
            </v-list-item>
            <v-divider :key="index" v-if="index!==(assets.length-1)"></v-divider>
          </template>
        </v-list>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialogDuration" max-width="320" persistent>
      <v-card>
        <v-card-title>Durasi</v-card-title>
        <v-card-text>
          <v-text-field type="number" label="Durasi (detik)"
            v-if="editedAsset!==null" v-model="editedAssetDuration"></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="dialogDuration=false"
            :disabled="loading"
          >Batal</v-btn>
          <v-btn text color="primary" @click="onOkDlgDurClick"
            :loading="loading"
          >Ok</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
"use strict"

import { API_URL, DEFAULT_PLAY_DURATION } from '@/config'
import { getWithAuth, postWithAuth } from '@/lib/fetch_utils'

export default {
  name: "PlaylistForm",
  data: () => ({
    playlist: {
      name: '',
      data: [],
      created: 0,
      updated: 0,
    },
    assets: [],
    dialogAssetList: false,
    dialogDuration: false,
    editedAssetDuration: 0,
    editedAssetIndex: -1,
    editedAsset: null,
    formDuration: 0,
    loading: false,
    nameRules: [
      v => !!v || 'Nama harus diisi',
      v => v.length >= 2 || 'Nama terlalu singkat'
    ],
  }),
  computed: {
    mode() {
      if (Object.prototype.hasOwnProperty.call(this.$route.query, 'mode')) {
        if ('edit' === this.$route.query.mode) {
          return 'edit'
        } else {
          return 'new'
        }
      } else {
        return 'new'
      }
    },
    playlistId() {
      if ('edit' === this.mode) {
        return this.$route.query.id
      } else {
        return undefined
      }
    },
    valid() {
      if ((this.playlist.name.length>0) && (this.playlist.data.length>0)) {
        return true
      } else {
        return false
      }
    }
  },
  watch: {
    loading: function() {
      this.$store.commit('SET_LOADING', this.loading)
    },
    //~ videoAssetPreview: function() {
      //~ if (!this.videoAssetPreview) {
        //~ this.$refs.videoPlayer.pause()
      //~ }
    //~ },
  },
  created() {
    this.$store.commit("SET_TOOLBAR", {
      title: this.mode==='new'?'Playlist Baru': 'Edit Playlist',
      backButton: true
    })
    if (this.mode==='edit') this.loadData()
  },
  methods: {
    loadData: async function() {
      this.loading = true
      let resp = await getWithAuth(`${API_URL}/playlists/${this.playlistId}`)
      this.loading = false
      if (200 !== resp.code) return
      this.playlist.name = resp.data.name
      this.playlist.created = resp.data.created
      this.playlist.updated = resp.data.updated
      this.playlist.data = JSON.parse(resp.data.data)
    },
    onAddAssetClick: async function() {
      if (this.assets.length === 0) {
        this.loading = true
        let resp = await getWithAuth(`${API_URL}/assets`)
        this.loading = false
        if (200!==resp.code) return
        this.assets = resp.data.rows
      }
      this.dialogAssetList = true
    },
    onCancelClick() {
      this.$router.go(-1)
    },
    onSaveClick() {
      if ('new'===this.mode) {
        this.addPlaylist()
      } else if ('edit'===this.mode) {
        this.updatePlaylist()
      }
    },
    onAlarmClick(asset, index){
      this.editedAsset = asset
      this.editedAssetIndex = index
      this.editedAssetDuration = asset[3] // FIXME! parseInt(asset[3])?
      this.dialogDuration = true
    },
    onOkDlgDurClick(){
      this.playlist.data[this.editedAssetIndex][3] = this.editedAssetDuration
      this.dialogDuration = false
    },
    onAssetListClick(asset) {
      let dur = DEFAULT_PLAY_DURATION
      if (asset[4] > 0) dur = asset[4] // FIXME! parseInt(asset[4])?
      this.playlist.data.push([asset[0], asset[1], asset[2], dur])
      this.dialogAssetList = false
    },
    moveDownAsset(index) {
      let _tmp = this.playlist.data[index]
      this.playlist.data.splice(index, 1)
      this.playlist.data.splice(index+1, 0, _tmp)
    },
    moveUpAsset(index) {
      let _tmp = this.playlist.data[index-1]
      this.playlist.data.splice(index-1, 1)
      this.playlist.data.splice(index, 0, _tmp)
    },
    deleteAsset(index){
      this.playlist.data.splice(index, 1)
    },
    addPlaylist: async function() {
      let pl_assets = []
      this.playlist.data.forEach(asset => {
        pl_assets.push([asset[0], asset[3]]) // FIXME! parseInt(asset[3])?
      })
      let params = {
        name: this.playlist.name,
        data: pl_assets
      }
      this.loading = true
      let resp = await postWithAuth(`${API_URL}/playlists`, params)
      this.loading = false
      if (200!==resp.code) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: resp.message
        })
        return
      }
      this.$store.commit('SHOW_SNACKBAR', {text: 'Playlist telah disimpan'})
      this.$router.go(-1)
    },
    updatePlaylist: async function() {
      let pl_assets = []
      this.playlist.data.forEach(asset => {
        pl_assets.push([asset[0], asset[3]]) // FIXME! parseInt(asset[3])?
      })
      let params = {
        name: this.playlist.name,
        data: pl_assets
      }
      this.loading = true
      let resp = await postWithAuth(`${API_URL}/playlists/${this.playlistId}`, params)
      this.loading = false
      if (200!==resp.code) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: resp.message
        })
        return
      }
      this.$store.commit('SHOW_SNACKBAR', {text: 'Playlist telah diperbaharui'})
      this.$router.go(-1)
    }
  }
}
</script>
